.Kids {

    section {
        background: url('../assets/pictures/kids-pexels-juan-salamanca-61129.jpg');
        background-size: cover;
        height: 80vw;
        display: flex;
        flex-direction: column;
    }

    h2 {
        margin: 5vw;
        color: white;
        text-shadow: 2px 2px 4px var(--navigation-blue);
    }

    .ages {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        height: 50vw;
        margin: 0 10vw;
        gap: 5%;
    }

    @media screen and (max-width: 600px) {

        section {
            height: 160vw;
            }
        
        .ages {
           height: 140vw;
           margin: 0 20vw;
           gap: 2%;
           grid-template-columns: 1fr;
           grid-template-rows: repeat(4, 1fr);
        }       
    }
}
